import button_push from "../images/button-push.png";
import button_push_off from "../images/button-push-off.png";

function notifications(window) {
	//  /  push-
	var pushElement = document.querySelector(".push");
	var pushImgElement = document.querySelector(".push__image");

	// ,   push-
	function isPushSupported() {
		//     push-
		if (Notification.permission === "denied") {
			alert("  push-");
			return;
		}

		//  push-  
		if (!("PushManager" in window)) {
			alert(", push-    .");
			return;
		}

		//  service-worker ,
		// ,     push-
		navigator.serviceWorker.ready.then(function (registration) {
			registration.pushManager
				.getSubscription()
				.then(function (subscription) {
					if (subscription) {
						changePushStatus(true);
					} else {
						changePushStatus(false);
					}
				})
				.catch(function (error) {
					console.error(" ", error);
				});
		});
	}

	//     push-
	function subscribePush() {
		navigator.serviceWorker.ready.then(function (registration) {
			if (!registration.pushManager) {
				alert("push-    .");
				return false;
			}

			// 
			registration.pushManager
				.subscribe({
					userVisibleOnly: true, //   
				})
				.then(function (subscription) {
					alert(" .");
					console.info("  push-.");
					console.log(subscription);
					changePushStatus(true);
				})
				.catch(function (error) {
					changePushStatus(false);
					console.error("   push-: ", error);
				});
		});
	}

	//   push-
	function unsubscribePush() {
		navigator.serviceWorker.ready.then(function (registration) {
			registration.pushManager
				.getSubscription()
				.then(function (subscription) {
					//   ,  
					if (!subscription) {
						alert("   push-.");
						return;
					}

					//  
					subscription
						.unsubscribe()
						.then(function () {
							alert(" .");
							console.info("push- .");
							console.log(subscription);
							changePushStatus(false);
						})
						.catch(function (error) {
							console.error(error);
						});
				})
				.catch(function (error) {
					console.error("    push-.");
				});
		});
	}

	//   (/ )
	function changePushStatus(status) {
		pushElement.dataset.checked = status;
		pushElement.checked = status;
		if (status) {
			pushElement.classList.add("active");
			pushImgElement.src = button_push;
		} else {
			pushElement.classList.remove("active");
			pushImgElement.src = button_push_off;
		}
	}

	//     /
	pushElement.addEventListener("click", function () {
		var isSubscribed = pushElement.dataset.checked === "true";
		if (isSubscribed) {
			unsubscribePush();
		} else {
			subscribePush();
		}
	});

	isPushSupported();
}

setTimeout(function () {
	notifications(window);
}, 1000);
